@use '@carbon/react/scss/themes';
@use '@carbon/react/scss/theme';
@use '@carbon/react/scss/components/button/tokens' as button-tokens;
@use '@carbon/themes/scss/utilities';
@use '@carbon/react/scss/components/button';
@use '@carbon/react/scss/components/text-input';
@use '@carbon/react/scss/components/tile';
@import'@carbon/react/scss/theme';

.app-side-container {
  box-sizing: border-box;
  width: 20rem;
  min-width: 20rem;
  align-items: center;
  align-self: stretch;
  display: flex;
  gap: 37px;
  z-index: 10;
  padding: 0;
  position: relative;
  background-color: $layer-01;
  flex-shrink: 4;
  display: flex;
  flex-direction: column;
  filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.20));
}
.tile-group {
  width: 100%;
}
.app-side-container .app-tittle-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  padding-top: 10%;
  margin-bottom: 10%;
  align-items: center;
  justify-content: start;
  margin-right: auto;
  margin-left: auto;
  height: 10%;
}
.app-tittle-container .logo {
  color: #fff;
  stroke: #fff;
  fill: #fff;
  width: 5rem;
  height: 5rem;
}
.app-tittle-container .title {
  padding-left: 20%;
  color: #fff;
}

.app-tittle-container .app-title {
  font-size: 2.5rem;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: center;
  position: relative;
  overflow-x: hidden;
}

.app-container {
  flex-grow: 4;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: start;
  overflow-y: hidden;
  overflow-x: hidden;
}

.tree-node {
  height: 3.5rem;
  div {
    font-size: 1rem;
    padding: 1.5rem;
  }
}