@use '@carbon/react/scss/themes';
@use '@carbon/react/scss/theme';
@use '@carbon/react/scss/components/button/tokens' as button-tokens;
@use '@carbon/themes/scss/utilities';
@use '@carbon/react/scss/components/button';
@use '@carbon/react/scss/components/text-input';
@import'@carbon/react/scss/themes';
@import'@carbon/react/scss/theme';


.pdf-viewer-container {
  width: 100%;
  height: 57rem;
}

.judgment-panel-container {
  width: 100%;
  height: 100%;
  padding: 3rem;
  box-sizing: border-box;
  overflow-y: scroll;
}

.judgment-panel-scroll {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
}

.panel-header {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.judgment-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  padding: 2rem;
  padding-bottom: 0;
  padding-top: 0;
  gap: 1rem;
  * {
    font-size: 1rem;
    padding: 0;
    font-weight: 400;
    margin: 0;
    color: $text-secondary;
  }
  .judgment-title {
    color: #fff;
    font-weight: 800;
    margin-bottom: 1rem;
    font-size: 2rem;
  }
}

.judgment-summary {
  width: 100%;
  font-size: 1rem;
  padding: 2rem;
  padding-top: 1rem;
  text-align: start;
  hr {
    color: #fff;
  }
  .summary-title {
    color: #fff;
  }
  .summary-title span {
    font-weight: 400;
    font-size: 0.8rem;
    color: $text-secondary;
  }
  .summary {
    margin-top: 1.5rem;
    color: $text-secondary;
    overflow-y: scroll;
    height: 15rem;
    font-weight: 400;
    font-size: 0.9rem;
  }
}
