@use '@carbon/react/scss/themes';
@use '@carbon/react/scss/theme';
@use '@carbon/react/scss/components/button/tokens' as button-tokens;
@use '@carbon/themes/scss/utilities';
@use '@carbon/react/scss/components/button';
@use '@carbon/react/scss/components/text-input';
@use '@carbon/react/scss/components/checkbox';
@use '@carbon/react/scss/components/fluid-text-input';
@use '@carbon/react/scss/components/search';
@use '@carbon/react/scss/components/fluid-search';
@use '@carbon/react/scss/components/contained-list';
@import '@carbon/react/scss/theme';
@import '@carbon/react/scss/themes';
@import '@carbon/react/scss/components/button';


.chatbox {
  height: 100%;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin: 2rem;
  margin-bottom: 0;
  box-sizing: border-box;
  .hidden {
    height: 0px;
  }
}

.message {
  margin-top: 0.2rem;
  padding: 0rem;
  width: 80%;
  * {
    text-align: left;
    font-size: 0.8rem;
  }
  p {
    margin-top: 0;
    margin-left: 2.3rem;
  }
  .message-header {
    display: flex;
    height: 2rem;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 0.2rem;
  }
}


.assistant-message {
  align-self: start;
  .message-logo {
    width: 2rem;
    height: 2rem;
    color: #fff;
    fill: #fff;
    stroke: #fff;
  }
}

.user-message {
  h4 {
    text-align: start;
  }
  .message-header::before {
    content: '';
    display: inline-block;
    width: 0.8rem;
    height: 0.8rem;
    margin-left: 0.5rem;
    background-color: $button-primary;
    border-radius: 50%;
    margin-right: 0.8rem;
  }
  .message-logo {
    display: none;
  }
  align-self: start;
}



.chat-container {
  height: 100%;
  width: 100%;
  background-color: $background;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.message-typing-box {
  padding: 5rem;
  padding-top: 2rem;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 2rem;
  align-items: end;
  
  .message-input {
    width: 2rem;
    display: inline-block;
  }
}

.chatbox-title {
  margin-top: 10%;
  font-size: 3rem;
  .hidden {
    height: 0px;
  }
}

.textlaw-list {
  height: 30rem;
  padding-top: 2rem;
  padding-left: 10rem;
  padding-right: 10rem;
  box-sizing: border-box;
  overflow-y: hidden;
  width: 100%;
  .hidden {
    height: 0px;
  }
  ul {
    height: 30rem;
    overflow-y: scroll;
  }
}